export default {
    name: "chatbar",
    props:{
        faceIcon:{
            type:String,
            defaultValue:"http://img4.imgtn.bdimg.com/it/u=3006607636,42691152&fm=11&gp=0.jpg"
        }
    },
    data() {
        return {
            InputBottom: 0,
            placeholder:"请文明发言"
        };
    },
    methods: {
        InputFocus(e) {
            this.InputBottom = e.detail.height
        },
        InputBlur(e) {
            this.InputBottom = 0
        }
    }
}
